.md {
  font-size: 1rem; }
  .md .demo-box {
    margin-bottom: 1rem; }
  .md-code-inline {
    background-color: #a3cfff;
    display: inline-block;
    margin: 0 0.2rem;
    padding: 0.3rem 0.4rem;
    line-height: 0.9rem;
    border-radius: 0.125rem; }
  .md-table {
    border-collapse: collapse;
    width: 100%;
    font-size: 1rem;
    line-height: 1.5rem; }
    .md-table td,
    .md-table th {
      border-bottom: 1px solid #dee2e6;
      padding: 1rem; }
    .md-table th {
      text-align: left;
      white-space: nowrap;
      color: #7a7c7f;
      font-weight: bold; }
    .md-table td {
      color: #212529; }
  .md-ul li, .md-ol li {
    margin-bottom: 0.5rem; }
