.demo-box {
  border: 1px solid #dee2e6;
  font-size: 1rem;
  background-color: #ffffff;
  border-radius: 0.25rem; }
  .demo-box .view {
    padding: 1rem;
    border-bottom: 1px solid #dee2e6; }
  .demo-box .info-wrapper {
    padding: 1rem;
    position: relative; }
    .demo-box .info-wrapper .title {
      position: absolute;
      top: -0.5rem;
      display: inline-block;
      background-color: #ffffff;
      padding: 0 0.33333rem;
      font-weight: bold; }
  .demo-box .code-wrapper {
    border-top: 1px dashed #dee2e6;
    padding: 1rem; }
    .demo-box .code-wrapper code {
      padding: 0;
      font-family: Menlo, Monaco, Consolas, Courier, monospace; }
    .demo-box .code-wrapper pre {
      margin: 0; }
